class Task {
    constructor(dims, startNode, endNode, graph, fnc) {
      this.dims = dims
      this.startNode = startNode
      this.endNode = endNode
      this.graph = graph
      this.fnc = fnc
    }
}

function generateRandomArr(length) {
    let arr = []
    for (let i = 0; i < length; i++) {
      arr.push(i)
    }
    for (let i = arr.length - 1; i > 0; i--) {
      let index = Math.floor(Math.random() * i)
      let temp = arr[i]
      arr[i] = arr[index]
      arr[index] = temp
    }
  
    return arr
}

function computeDistance(Mat, startNode, endNode, C) {
    let dim = Mat.length - 1
    C = C.filter(v => v <= dim)
    C = C.filter(v => v !== 0)
  
    let dis = 0
    for (let i = 0; i < C.length - 1; i++) {
      if (Mat[C[i]][C[i + 1]] === 0) {
        dis = Infinity
        break
      }
      dis += Mat[C[i]][C[i + 1]]
    }
  
    if (C.length === 0 || C[C.length - 1] !== endNode) {
      dis = Infinity
    }
  
    return dis
}

function Dijkstra(Mat, startNode) {
  let n = Mat.length - 1
  let visited = new Array(n + 1).fill(false)
  let dist = new Array(n + 1).fill(Infinity)
  let prev = new Array(n + 1).fill(0)
  for (let i = 1; i <= n; i++) {
    dist[i] = Mat[startNode][i] || Infinity
    if (dist[i] === Infinity) {
      prev[i] = 0
    } else {
      prev[i] = startNode
    }
  }
  dist[startNode] = 0
  visited[startNode] = true

  for (let i = 2; i <= n; i++) {
    let node = startNode
    let maxx = Infinity
    for (let j = 1; j <= n; j++) {
      if (!visited[j] && dist[j] < maxx) {
        node = j
        maxx = dist[j]
      }
    }
    visited[node] = true

    for (let j = 1; j <= n; j++) {
      if (!visited[j] && Mat[node][j]) {
        let newDist = dist[node] + Mat[node][j]
        if (newDist < dist[j]) {
          dist[j] = newDist
          prev[j] = node
        }
      }
    }
  }

  return prev
}

function findPath(prev, startNode, endNode) {
  let path = []
  path.unshift(endNode)
  let temp = prev[endNode]
  while (temp !== startNode) {
    path.unshift(temp)
    temp = prev[temp]
  }
  path.unshift(startNode)
  return path
}

function transformUnvalid(y1, i1, Mat1, dim1, endNode, object1, task, D_multitask) {
    let len1 = y1.filter(v => v).length
    let index = i1
    let newY1 = y1.slice(0, i1 + 1)
    let cannotReach = new Array(dim1 + 1).fill(false)

    let steps = []
    let adds = []
    let step

    while (index < len1) {
        let mid = y1[index + 1]
        if (mid > dim1 || Mat1[y1[index]][mid] === 0 || cannotReach[mid]) {
            step = 1
            while (true) {
                step++
                if (index + step < len1) {
                    let after = y1[index + step]
                    while (after > dim1 || cannotReach[after]) {
                        step++
                        if (index + step >= y1.length) {
                          initialize(object1, D_multitask, task)
                          return object1.rnvec
                        }
                        after = y1[index + step]
                    }

                    let visited1 = new Array(dim1 + 1).fill(false)
                    for (let i = 0; i < newY1.length; i++) {
                        visited1[newY1[i]] = true
                    }

                    let before = y1[index]
                    let beforeNodes = []
                    for (let i = 1; i <= dim1; i++) {
                        if (Mat1[before][i] && !visited1[i]) {
                            beforeNodes.push(i)
                        }
                    }

                    let afterNodes = []
                    for (let i = 1; i <= dim1; i++) {
                        if (Mat1[after][i] && !visited1[i]) {
                            afterNodes.push(i)
                        }
                    }

                    let comm = beforeNodes.filter(v => afterNodes.indexOf(v) > -1)
                    if (comm.length) {
                        let node = comm[Math.floor(Math.random() * comm.length)]
                        newY1.push(node)
                        newY1.push(after)
                        index += step

                        steps.push(step)
                        adds.push(2)
                        break
                    } else if (Mat1[before][after]) {
                        index += step
                        newY1.push(after)

                        steps.push(step)
                        adds.push(1)

                        break
                    } else if (index + step === len1 - 1) {
                        cannotReach[before] = true
                        if (steps.length === 0) {
                            initialize(object1, D_multitask, task)
                            return object1.rnvec
                        }

                        index -= steps.pop()
                        newY1.splice(newY1.length - adds.pop())

                        if (index <= 0) {
                            initialize(object1, D_multitask, task)
                            return object1.rnvec
                        }

                        break
                    }
                } else {
                    cannotReach[y1[index]] = true
                    index -= steps.pop()

                    newY1.splice(newY1.length - adds.pop())

                    if (index <= 0) {
                        initialize(object1, D_multitask, task)
                        return object1.rnvec
                    }

                    break
                }
            }
        } else {
            index++
            newY1.push(mid)
            steps.push(1)
            adds.push(1)
        }
    }
    return newY1
}

function eliminateLoop(y) {
    let uniqueY = [...new Set(y)]
  
    let lenY = y.filter(x => x).length
    let lenUniY = uniqueY.filter(x => x).length
  
    while (lenY !== lenUniY) {
      for (let i = 1; i < lenY - 1; i++) {
        let next = y.indexOf(y[i], i + 1)
        if (next !== -1) {
          y = [...y.slice(0, i + 1), ...y.slice(next + 1, lenY)]
          break
        }
      }
  
      uniqueY = [...new Set(y)]
  
      lenY = y.filter(x => x).length
      lenUniY = uniqueY.filter(x => x).length
    }
}

function RouletteWheelSelection(arrayInput) {
    let len = arrayInput.length
    let index = 0
    if (len === 1) {
      index = 0
      return index
    }
  
    if (!arrayInput.find(v => v < 1)) {
      let minVal = Math.min(...arrayInput)
      if (minVal) {
        arrayInput = arrayInput.map(v => 1 / minVal * v)
      } else {
        let temp = arrayInput
        for (let i = 0; i < temp.length; i++) {
          if (!temp[i]) {
            temp[i] = Infinity
          }
        }
        let minTempVal = Math.min(...temp)
        arrayInput = arrayInput.map(v => 1 / minTempVal * v)
      }
  
    }
  
    let temp = 0
    let tempProb = new Array(len).fill(0)
  
    for (let i = 0; i < len; i++) {
      tempProb[i] = temp + arrayInput[i]
      temp = tempProb[i]
    }
  
    let i = parseInt(Math.random() * Math.floor(tempProb[tempProb.length - 1])) + 1
    let item = tempProb.find(v => v >= i)
    index = tempProb.indexOf(item)
    return index
}

function localSearch(p, task, Tasks, no_of_tasks) {
  let x = p.rnvec
  let Mat = task.graph
  let dim = task.dims
  let startNode = task.startNode
  let endNode = task.endNode

  let len = x.filter(v => v).length
  let i = 0
  let newX = []

  while (i < len - 2) {
      let before = x[i]
      let mid = x[i + 1]
      let step = 2
      let initValue = Mat[before][mid]
      let last = mid
      let tag = true

      while (tag) {
          let after = x[i + step]
          initValue += Mat[last][after]
          last = after

          let visited = new Array(dim + 1).fill(false)
          for (let i = 0; i < newX.length; i++) {
              visited[newX[i]] = true
          }
          visited[startNode] = visited[endNode] = true

          let beforeNodes = []
          for (let j = 1; j <= dim; j++) {
              if (Mat[before][j] && !visited[j]) {
                  beforeNodes.push(j)
                  visited[j] = true
              }
          }

          visited = new Array(dim + 1).fill(false)
          for (let i = 0; i < newX.length; i++) {
              visited[newX[i]] = true
          }
          visited[startNode] = visited[endNode] = true

          let afterNodes = []
          for (let j = 1; j <= dim; j++) {
              if (Mat[after][j] && !visited[j]) {
                  afterNodes.push(j)
                  visited[j] = true
              }
          }

          let comm = beforeNodes.filter(v => afterNodes.indexOf(v) > -1)
          if (comm.length) {
              for (let node of comm) {
                  if (Mat[before][node] + Mat[node][after] < initValue) {
                      newX.push(before)
                      i = i + step - 1
                      x[i] = node
                      tag = false
                      break
                  }
              }
          }

          if (!tag) {
              break
          }

          step++

          if (i + step >= len) {
              newX.push(before)
              i++
              break
          }
      }
  }

  newX.push(x[len - 2])
  newX.push(endNode)
  eliminateLoop(newX)

  p.rnvec = newX
  evaluate(p, Tasks, no_of_tasks)
}

class Chromosome {
    constructor(no_of_tasks, rnvec, factorial_costs, factorial_ranks, scalar_fitness, skill_factor) {
      this.rnvec = rnvec
      this.factorial_costs = factorial_costs || new Array(no_of_tasks)
      this.factorial_ranks = factorial_ranks || new Array(no_of_tasks)
      this.scalar_fitness = scalar_fitness || Infinity
      this.skill_factor = skill_factor || 0
    }
}

function initialize(object, D, Task) {
    let startNode = Task.startNode
    let endNode = Task.endNode
    let graph = Task.graph
    let dims = Task.dims
    let nvars = new Array(D).fill(0)
    let visited = new Array(D + 1).fill(false)
    nvars[0] = startNode
    visited[startNode] = true
    let tag = true
    let noPath = 0
    
    while (tag) {
      for (let i = 1; i < dims; i++) {
        let before = nvars[i - 1]
        let connected = []
        let count = 0
        for (let j = 1; j <= dims; j++) {
          if (graph[before][j] !== 0 && !visited[j]) {
            connected[count++] = j
          }
        }

        if (count) {
          let node = connected[Math.floor(Math.random() * count)]
          nvars[i] = node
          visited[node] = true
        } else {
          nvars = new Array(D).fill(0)
          visited = new Array(D + 1).fill(false)
          nvars[0] = startNode
          visited[startNode] = true

          noPath++
          if (noPath === 1000) {
            nvars = [startNode, endNode]
            tag = false
          }
          break
        }

        if (nvars[i] === endNode) {
          tag = false
          break
        }
      }
    }

    object.rnvec = nvars
    if (noPath === 1000) {
      return false
    } else {
      return true
    }
}

function evaluate(object, Tasks, no_of_tasks) {
    let calls = 0
    if (object.skill_factor === 0) {
      for (let i = 0; i < no_of_tasks; i++) {
        object.factorial_costs[i] = Tasks[i].fnc(object.rnvec)
        calls++
      }
    } else {
      object.factorial_costs.fill(Infinity)
      for (let i = 0; i < no_of_tasks; i++) {
        if (object.skill_factor - 1 === i) {
          object.factorial_costs[object.skill_factor - 1] = Tasks[object.skill_factor - 1].fnc(object.rnvec)
          calls = 1
          break
        }
      }
    }

    return calls
}

function evaluate_SOO(object, Task) {
    object.factorial_costs[0] = Task.fnc(object.rnvec)
    let calls = 1
    object.skill_factor = 1
    return calls
}

function crossover(object1, object2, p1, p2, Tasks, D_multitask) {
    let x1 = p1.rnvec
    let x2 = p2.rnvec
    let dim1 = Tasks[p1.skill_factor - 1].dims
    let dim2 = Tasks[p2.skill_factor - 1].dims
    let startNode = Tasks[p1.skill_factor - 1].startNode
    let endNode = Tasks[p1.skill_factor - 1].endNode
    let Mat1 = Tasks[p1.skill_factor - 1].graph
    let Mat2 = Tasks[p2.skill_factor - 1].graph

    let common = x1.filter(item => x2.indexOf(item) > -1)
    common = common.filter(x => !(x === startNode || x === endNode || x === 0))

    if (common.length) {
      let node = common[Math.floor(Math.random() * common.length)]
      let i1 = x1.indexOf(node)
      let i2 = x2.indexOf(node)

      let y1 = [...x1.slice(0, i1 + 1), ...x2.slice(i2 + 1)]
      let y2 = [...x2.slice(0, i2 + 1), ...x1.slice(i1 + 1)]

      eliminateLoop(y1)
      eliminateLoop(y2)

      if (p1.skill_factor !== p2.skill_factor) {
        y1 = transformUnvalid(y1, 0, Mat1, dim1, endNode, object1, Tasks[p1.skill_factor - 1], D_multitask)
        y2 = transformUnvalid(y2, 0, Mat2, dim2, endNode, object2, Tasks[p2.skill_factor - 1], D_multitask)
      }

      object1.rnvec = y1
      object2.rnvec = y2
    } else {
      initialize(object1, D_multitask, Tasks[p1.skill_factor - 1]);
      initialize(object2, D_multitask, Tasks[p2.skill_factor - 1]);
    }
            
}

function mutate(object, p, Tasks, D_multitask) {
    let x = p.rnvec
    let graph = Tasks[p.skill_factor - 1].graph
    let dims = Tasks[p.skill_factor - 1].dims
    let len = x.filter(v => v).length

    // let subX = x.slice(1, len - 1)
    if (len < 4) {
      initialize(object, D_multitask, Tasks[p.skill_factor - 1])
      object.skill_factor = p.skill_factor
      return
    }

    let indexI1 = Math.floor(Math.random() * (len - 2))
    let indexI2
    if (indexI1 > (len - 2) / 2) {
      indexI2 = Math.floor(Math.random() * indexI1)
      let temp = indexI1
      indexI1 = indexI2
      indexI2 = temp
    } else {
      indexI2 = Math.floor(Math.random() * (len - 1 - indexI1)) + indexI1 + 1
    }

    let i1 = x[indexI1]
    let i2 = x[indexI2]

    let subPath = []
    let visited = new Array(dims + 1).fill(false)
    for (let i = 0; i <= indexI1; i++) {
      visited[x[i]] = true
    }
    for (let i = indexI2 + 1; i < len; i++) {
      visited[x[i]] = true
    }

    let before = i1
    while (true) {
      let connected = []
      for (let j = 1; j <= dims; j++) {
        if (graph[before][j] && !visited[j]) {
          connected.push(j)
        }
      }

      if (connected.length) {
        let node = connected[Math.floor(Math.random() * connected.length)]
        subPath.push(node)
        visited[node] = true
        before = node

        if (node === i2) {
          break
        }
      } else {
        subPath = []
        visited = new Array(dims + 1).fill(false)
        for (let i = 0; i <= indexI1; i++) {
          visited[x[i]] = true
        }
        for (let i = indexI2 + 1; i < len; i++) {
          visited[x[i]] = true
        }
        before = i1
      }
    }

    let y = [...x.slice(0, indexI1 + 1), ...subPath, ...x.slice(indexI2 + 1)]
    object.rnvec = y
}

function generateGraph(n, numOfEdge) {
    const Mat = new Array(n + 1)
    for (let i = 0; i <= n; i++) {
      Mat[i] = new Array(n + 1).fill(0)
    }
  
    for (let i = 0; i < numOfEdge; i++) {
      let start = Math.floor(Math.random() * n) + 1
      let end = Math.floor(Math.random() * n) + 1
      if (start === end) {
        continue
      }
  
      let len = Math.floor(Math.random() * n) + 1
      if (Mat[start][end] === 0) {
        Mat[start][end] = len
        Mat[end][start] = len
      }
    }
  
    return Mat
}

function calcDist(Mat, path) {
  let sum = 0
  for (let i = 0; i < path.length - 1; i++) {
    sum += Mat[path[i]][path[i + 1]]
  }
  return sum
}
  

export function MAIN(pop, gen, rmp, selection_pressure, startNode, endNode, numOfNetwork, isRandom, payload) {
    const Tasks = new Array(numOfNetwork)
    const bestobj_dijk = []
    const bestInd_data_dijk = []
    const Mats = []
    const minDist = []

    if (isRandom) {
      for (let i = 0; i < numOfNetwork; i++) {
        let A = generateGraph(payload.numOfNode, payload.numOfEdge)
        Mats.push(A)

        let prev = Dijkstra(A, startNode)
        let path = [startNode, endNode]
        let dist = Infinity
        if (prev[endNode] !== 0) {
          path = findPath(prev, startNode, endNode)
          dist = calcDist(A, path)
          minDist.push(dist)
        }
        bestobj_dijk.push(dist)
        bestInd_data_dijk.push(path)

        let fnc = computeDistance.bind(this, A, startNode, endNode)
        Tasks[i] = new Task(payload.numOfNode, startNode, endNode, A, fnc)
      }
    } else {
      for (let i = 0; i < numOfNetwork; i++) {
        let A = payload[i]

        let prev = Dijkstra(A, startNode)
        let path = [startNode, endNode]
        let dist = Infinity
        if (prev[endNode] !== 0) {
          path = findPath(prev, startNode, endNode)
          dist = calcDist(A, path)
          minDist.push(dist)
        }

        bestobj_dijk.push(dist)
        bestInd_data_dijk.push(path)

        let fnc = computeDistance.bind(this, A, startNode, endNode)
        Tasks[i] = new Task(A.length - 1, startNode, endNode, A, fnc)
      }
    }
    
    const {bestobj, EvBestFitness,  D_multitask, bestInd_data: bestInd_data_allPro, EvBestAccuracy} = MFEA(Tasks, pop, gen, selection_pressure, rmp, minDist)

    const bestInd_data = []
    for (let population of bestInd_data_allPro) {
      bestInd_data.push(population.rnvec)
    }

    if (isRandom) {
      return {
        Mats,
        EvBestFitness,
        EvBestAccuracy,
        bestobj,
        bestInd_data,
        bestobj_dijk,
        bestInd_data_dijk
      }
    } else {
      return {
        Mats: payload,
        EvBestFitness,
        EvBestAccuracy,
        bestobj,
        bestInd_data,
        bestobj_dijk,
        bestInd_data_dijk
      }
    }
}

function MFEA(Tasks, pop, gen, selection_process, rmp, minDist) {
    if (pop % 2) {
      pop++
    }
  
    let no_of_tasks = Tasks.length
    let D = new Array(no_of_tasks).fill(0)
    for (let i = 0; i < no_of_tasks; i++) {
      D[i] = Tasks[i].dims
    }
  
    let D_multitask = Math.max(...D)

    // 处理没有路的情况
    let noPathTasksId = []
    for (let i = 0; i < Tasks.length; i++) {
      let population_test = new Chromosome(no_of_tasks)
      if (!initialize(population_test, D_multitask, Tasks[i])) {
        noPathTasksId.push(i)
      }
    }

    let noPathTasks = []
    for (let i = 0; i < noPathTasksId.length; i++) {
      let task = Tasks.splice(noPathTasksId[i] - i, 1)
      noPathTasks = noPathTasks.concat(task)
    }

    no_of_tasks = Tasks.length
    D = new Array(no_of_tasks).fill(0)
    for (let i = 0; i < no_of_tasks; i++) {
      D[i] = Tasks[i].dims
    }
  
    let fnceval_calls = 0
    let calls_per_individual = new Array(pop).fill(0)
    let EvBestFitness = new Array(no_of_tasks)
    let EvBestAccuracy = new Array(no_of_tasks)
    for (let i = 0; i < no_of_tasks; i++) {
      EvBestFitness[i] = new Array(gen).fill(0)
      EvBestAccuracy[i] = new Array(gen).fill(0)
    }
    let TotalEvaluations = new Array(gen).fill(0)
    let bestobj = new Array(no_of_tasks).fill(Infinity)
    let bestInd_data = new Array(no_of_tasks)

    if (no_of_tasks === 0) {
      for (let i = 0; i < noPathTasksId.length; i++) {
        bestobj.splice(noPathTasksId[i], 0, Infinity)
        bestInd_data.splice(noPathTasksId[i], 0, {rnvec: []})
        EvBestFitness.splice(noPathTasksId[i], 0, [])
        EvBestAccuracy.splice(noPathTasksId[i], 0, [])
      }
      return {bestobj, bestInd_data, EvBestFitness, EvBestAccuracy}
    }
  
    let population = new Array(pop)
  
    for (let i = 0; i < pop; i += no_of_tasks) {
      for (let j = 0; j < no_of_tasks; j++) {
        if (i + j >= pop) {
          break
        }
        population[i + j] = new Chromosome(no_of_tasks)
        initialize(population[i + j], D_multitask, Tasks[j])
        population[i + j].skill_factor = j + 1
      }
    }
  
    for (let i = 0; i < pop; i++) {
      calls_per_individual[i] = evaluate(population[i], Tasks, no_of_tasks)
    }
  
    let sum = calls_per_individual.reduce((a, b) => a + b, 0)
    fnceval_calls += sum
    TotalEvaluations[0] = fnceval_calls
  
    let factorial_cost = new Array(pop).fill(0)
  
    for (let i = 0; i < no_of_tasks; i++) {
      for (let j = 0; j < pop; j++) {
        factorial_cost[i] = {value: population[j].factorial_costs[i], index: i}
      }
  
      // factorial_cost.sort((itemA, itemB) => itemA.value - itemB.value)
      // let newPopulation = []
      // for (let i = 1; i <= pop; i++) {
      //   newPopulation[i] = population[factorial_cost[i].index]
      // }
      // population = newPopulation
      population.sort((itemA, itemB) => itemA.factorial_costs[i] - itemB.factorial_costs[i])
      for (let j = 0; j < pop; j++) {
        population[j].factorial_ranks[i] = j + 1
      }
  
      bestobj[i] = population[0].factorial_costs[i]
      EvBestFitness[i][0] = bestobj[i]
      EvBestAccuracy[i][0] = minDist[i] / bestobj[i]
      bestInd_data[i] = population[0]
    }
  
    for (let i = 0; i < pop; i++) {
      let minRank = Math.min(...population[i].factorial_ranks)
      let x = population[i].factorial_ranks.filter(v => v === minRank)
      let equivalent_skills = x.length
      if (equivalent_skills > 1) {
        population[i].skill_factor = x[Math.floor(Math.random() * equivalent_skills)]
        let tmp = population[i].factorial_costs[population[i].skill_factor - 1]
        population[i].factorial_costs.fill(Infinity)
        population[i].factorial_costs[population[i].skill_factor - 1] = tmp
      } else {
        population[i].skill_factor = population[i].factorial_ranks.indexOf(minRank) + 1
        let tmp = population[i].factorial_costs[population[i].skill_factor - 1]
        population[i].factorial_costs.fill(Infinity)
        population[i].factorial_costs[population[i].skill_factor - 1] = tmp
      }
    }
  
    let generation = 0
    while (generation < gen) {
      let inorder = generateRandomArr(pop)
      let count = 0
      let child = new Array(pop)
      for (let i = 0; i < pop / 2; i++) {
        let p1 = inorder[i]
        let p2 = inorder[i + pop / 2]
        child[count] = new Chromosome(no_of_tasks)
        child[count + 1] = new Chromosome(no_of_tasks)
        if (population[p1].skill_factor == population[p2].skill_factor || Math.random() < rmp) {
          crossover(child[count], child[count + 1], population[p1], population[p2], Tasks, D_multitask)
          child[count].skill_factor = population[p1].skill_factor
          child[count + 1].skill_factor = population[p2].skill_factor
          // console.log('crossover:', Tasks[child[count].skill_factor - 1].fnc(child[count].rnvec), 
          // Tasks[child[count + 1].skill_factor - 1].fnc(child[count + 1].rnvec), 
          // ' parent:', Tasks[population[p1].skill_factor - 1].fnc(population[p1].rnvec), 
          // Tasks[population[p2].skill_factor - 1].fnc(population[p2].rnvec))
        } else {
          mutate(child[count], population[p1], Tasks, D_multitask)
          child[count].skill_factor = population[p1].skill_factor
          mutate(child[count + 1], population[p2], Tasks, D_multitask)
          child[count + 1].skill_factor = population[p2].skill_factor
        }
        count += 2
      }
  
      for (let i = 0; i < pop; i++) {
        calls_per_individual[i] = evaluate(child[i], Tasks, no_of_tasks)
      }
      fnceval_calls += calls_per_individual.reduce((a, b) => a + b, 0)
      TotalEvaluations[generation] = fnceval_calls
  
      let intpopulation = [...population, ...child]
      let factorial_cost = new Array(2 * pop).fill(0)
  
      for (let i = 0; i < no_of_tasks; i++) {
        for (let j = 0; j < 2 * pop; j++) {
          factorial_cost[j] = {value: intpopulation[j].factorial_costs[i], index: j}
        }

        intpopulation.sort((itemA, itemB) => itemA.factorial_costs[i] - itemB.factorial_costs[i])
        for (let j = 0; j < 2 * pop; j++) {
          intpopulation[j].factorial_ranks[i] = j + 1;
        }
  
        if (intpopulation[0].factorial_costs[i] <= bestobj[i]) {
          bestobj[i] = intpopulation[0].factorial_costs[i]
          bestInd_data[i] = intpopulation[0]
        }
        EvBestFitness[i][generation] = bestobj[i]
        EvBestAccuracy[i][generation] = minDist[i] / bestobj[i]
      }
  
      for (let i = 0; i < 2 * pop; i++) {
        let minRank = Math.min(...intpopulation[i].factorial_ranks)
        intpopulation[i].skill_factor = intpopulation[i].factorial_ranks.indexOf(minRank) + 1
        intpopulation[i].scalar_fitness = 1 / minRank
      }
  
      if (selection_process === 'elitist') {
        let scalar_fitnesses = []
        for (let i = 0; i < 2 * pop; i++) {
          scalar_fitnesses.push({value: intpopulation[i].scalar_fitness, index: i})
        }
  
        intpopulation.sort((itemA, itemB) => itemB.scalar_fitness - itemA.scalar_fitness)
        population = intpopulation.slice(1, pop)
      } else if (selection_process === 'roulette wheel') {
        let skill_group = new Array(no_of_tasks)
        for (let i = 0; i < no_of_tasks; i++) {
          skill_group[i] = intpopulation.filter(v => v.skill_factor - 1 === i)
        }
  
        let count = 0
        while (count < pop) {
          let skill = count % no_of_tasks
          population[count++] = skill_group[skill][RouletteWheelSelection([skill_group[skill].scalar_fitness])]
        }
      }

      for (let i = 0; i < pop; i++) {
        let individual = population[i]
        let bestTask = individual.skill_factor - 1
        localSearch(individual, Tasks[bestTask], Tasks, no_of_tasks)
        let nowCost = individual.factorial_costs[bestTask]
        if (nowCost < bestobj[bestTask]) {
          bestobj[bestTask] = nowCost
          bestInd_data[bestTask] = individual
          EvBestFitness[bestTask][generation] = bestobj[bestTask]
          EvBestAccuracy[bestTask][generation] = minDist[i] / bestobj[bestTask]
        }
      }
      generation++
    }
  
    for (let i = 0; i < noPathTasksId.length; i++) {
      bestobj.splice(noPathTasksId[i], 0, Infinity)
      bestInd_data.splice(noPathTasksId[i], 0, {rnvec: []})
      EvBestFitness.splice(noPathTasksId[i], 0, [])
      EvBestAccuracy.splice(noPathTasksId[i], 0, [])
    }
    return {bestobj, bestInd_data, EvBestFitness, EvBestAccuracy, D_multitask}
}

function SOO(Task, Tasks, D_multitask, rmp, task_for_comparison_with_SOO, pop, gen, selection_process) {    
    if (pop % 2) {
      pop = pop + 1
    }
    
    let D = Task.dims
    
    let no_of_tasks = Tasks.length

    let fnceval_calls = 0
    let calls_per_individual = new Array(pop).fill(0)
    let EvBestFitness = new Array(gen).fill(0)
    let TotalEvaluations = new Array(gen).fill(0)

    let population = new Array(pop)
  
    for (let i = 0; i < pop; i++) {
      population[i] = new Chromosome(no_of_tasks)
      initialize(population[i], D_multitask, Task)
      population[i].skill_factor = 1
    }

    for (let i = 0; i < pop; i++) {
      calls_per_individual[i] = evaluate_SOO(population[i], Task)
    }

    let sum = calls_per_individual.reduce((a, b) => a + b, 0)
    fnceval_calls += sum
    TotalEvaluations[0] = fnceval_calls

    let factorial_cost = new Array(pop).fill(0)
    let bestobj = Infinity
    let bestInd_data = null
   
    for (let j = 0; j < pop; j++) {
      factorial_cost[j] = population[j].factorial_costs[0]
    }
    bestobj = Math.min(...factorial_cost)
    bestInd_data = population[factorial_cost.indexOf(bestobj)]
    EvBestFitness[0] = bestobj
    
    let generation = 1
    let child = new Array(pop)
    while (generation < gen) {
      let inorder = generateRandomArr(pop)
      let count = 0
      for (let i = 0; i < pop / 2; i++) {
        let p1 = inorder[i] 
        let p2 = inorder[i + pop / 2]
        child[count] = new Chromosome(no_of_tasks)
        child[count + 1] = new Chromosome(no_of_tasks)

        crossover(child[count], child[count + 1], population[p1], population[p2], Tasks, D_multitask)
        child[count].skill_factor = 1
        child[count+1].skill_factor = 1
          if (Math.random() < rmp) {
              mutate(child[count], population[p1], Tasks, D_multitask)
              child[count].skill_factor = 1
              mutate(child[count + 1], population[p2], Tasks, D_multitask)
              child[count + 1].skill_factor = 1
          }            
          count = count + 2
      }      
      for (let i = 0; i < pop; i++) {
        calls_per_individual[i] = evaluate_SOO(child[i], Task)           
      }
      
      fnceval_calls += calls_per_individual.reduce((a, b) => a + b, 0)
      TotalEvaluations[generation] = fnceval_calls
      
      let intpopulation = [...population, ...child]

      intpopulation.sort((itemA, itemB) => itemA.factorial_costs[0] - itemB.factorial_costs[0])

      for (let i = 0; i < 2 * pop; i++) {
        intpopulation[i].scalar_fitness = 1 / (i + 1)
      }

      if (intpopulation[0].factorial_costs[0] <= bestobj) {
        bestobj = intpopulation[0].factorial_costs[0]
        bestInd_data = intpopulation[0]
      }

      
      EvBestFitness[generation] = bestobj

      if (selection_process === 'elitist') {
        intpopulation.sort((itemA, itemB) => itemB.scalar_fitness - itemA.scalar_fitness)
        population = intpopulation.slice(1, pop)
      } else if (selection_process === 'roulette wheel') {
        let scalar_fitnesses = []
        for (let i = 0; i < 2 * pop; i++) {
          scalar_fitnesses.push(intpopulation[i].scalar_fitness)
        }
       
        for (let i = 0; i < pop; i++) {
          population[i] = intpopulation[RouletteWheelSelection(scalar_fitnesses)]
        }
        
      }
      generation++
    }   
   
    return EvBestFitness
}

