<template>
  <div class="result">
    <el-form :inline="true" :model="formObj" :rules="rules" ref="paramForm">
      <el-form-item label="起点" prop="startNode">
        <el-input v-model.number="formObj.startNode" type="number"></el-input>
      </el-form-item>
      <el-form-item label="终点" prop="endNode">
        <el-input v-model.number="formObj.endNode" type="number"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="success" :disabled="disabled" @click="onSubmit">运行</el-button>
        <el-button type="success" @click="handleShowIterativeCurve">迭代曲线</el-button>
        <el-button type="success" @click="handleShowAccuracyCurve">正确度曲线</el-button>
      </el-form-item>
    </el-form>
    <div class="mat-wrap">
      <div v-for="index_ in numOfNetwork" 
      :key="index_" 
      v-loading="loading" 
      class="mat"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.5)"
      >
        <div :id="`mat${index_}`" class="graph"></div>
        <el-table :data="tableDatas[index_ - 1]">
          <el-table-column prop="id" label="网络编号" align="center"></el-table-column>
          <el-table-column prop="mfea" align="center">
            <template slot="header">
              <el-button type="text" @click="showMfeaPath(index_ - 1)">MFEA</el-button>
            </template>
          </el-table-column>
          <el-table-column prop="dijkstra" align="center">
            <template slot="header">
              <el-button type="text" @click="showDijkstraPath(index_ - 1)">Dijkstra</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <el-dialog :visible.sync="showIterativeCurve" center title="不同网络的代价因子随迭代次数的变化曲线">
       <div id="myChart2" class="chart" ref="myChart2" style="height: 200px; width: 100%"></div>
    </el-dialog>
    <el-dialog :visible.sync="showAccuracyCurve" center title="不同网络的正确度随迭代次数的变化曲线">
       <div id="myChart3" class="chart" ref="myChart3" style="height: 200px; width: 100%"></div>
    </el-dialog>
  </div>
</template>


<script>
import { MAIN } from './algorithm'
import { mapState } from 'vuex'
import * as vis from 'vis-network/dist/vis-network.min.js'
import 'vis-network/dist/dist/vis-network.min.css'


export default {
  name: 'About',
  components: {
    
  },
  computed: {
    numOfNetwork() {
      if (this.form.inputWay === 'random') {
        return this.randomNetwork.numOfNetwork
      } else {
        return this.Mats.length
      }
    },
    maxDim() {
      if (this.form.inputWay === 'random') {
        return this.randomNetwork.numOfNode
      } else {
        let lens = this.Mats.map(arr => arr.length - 1)
        return Math.min(...lens)
      }
    },
    ...mapState(['form', 'Mats', 'randomNetwork'])
  },
  data() {
    let checkInput = (rule, value, callback) => {
      if (!value) {
        return callback(new Error('输入值不能为空'))
      }
      const maxDim = this.maxDim
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error('请输入数字'))
        } else {
          if (value < 0) {
            callback(new Error('请输入正数'))
          } else if (value > maxDim) {
            callback(new Error('输入值不能大于网络的最大维度'))
          } else {
            callback()
          }
        }
      }, 1000)
    }
    return {
      Mat: [],
      path: [],
      loading: false,
      disabled: false,
      formObj: {
        startNode: '',
        endNode: ''
      },
      rules: {
        startNode: [{
          validator: checkInput, trigger: 'blur'
        }],
        endNode: [{
          validator: checkInput, trigger: 'blur'
        }]
      },
      tableDatas: [],
      showIterativeCurve: false,
      showAccuracyCurve: false,
      lineOptions: {},
      accuracyLineOptions: {}
    }
  },
  created() {
  },
  mounted() {
  },
  methods: {
    handleShowIterativeCurve() {
      this.showIterativeCurve = true
      this.$nextTick(() => {
        let myChart = this.$echarts.init(this.$refs.myChart2)
        myChart.setOption(this.lineOptions, true)
      })
    },
    handleShowAccuracyCurve() {
      this.showAccuracyCurve = true
      this.$nextTick(() => {
        let myChart = this.$echarts.init(this.$refs.myChart3)
        myChart.setOption(this.accuracyLineOptions, true)
      })
    },
    showMfeaPath(index) {
      if (this.tableDatas[index]) {
        let path = this.tableDatas[index][0]['mfeaPath'].join('->')
        this.$alert(path, 'MFEA求得的最短路径为', {
          confirmButtonText: '确定'
        }).catch(() => {})
      }
    },
    showDijkstraPath(index) {
      if (this.tableDatas[index]) {
        let path = this.tableDatas[index][0]['dijkstraPath'].join('->')
        this.$alert(path, 'Dijkstra求得的最短路径为', {
          confirmButtonText: '确定'
        }).catch(() => {})
      }
    },
    onSubmit() {
      this.$refs['paramForm'].validate((valid) => {
        if (valid) {
          this.loading = true
          this.disabled = true
          const startNode = +this.formObj.startNode
          const endNode = +this.formObj.endNode
          let isRandom = this.form.inputWay === 'random' ? true : false
          let payload = this.Mats
          if (isRandom) {
            payload = this.randomNetwork
          }

          let {
            Mats,
            EvBestFitness,
            EvBestAccuracy,
            bestobj,
            bestInd_data,
            bestobj_dijk,
            bestInd_data_dijk
          } = MAIN(this.form.populationSize, this.form.generationCount, this.form.rmp, 
          this.form.selection_process, startNode, endNode, this.numOfNetwork, isRandom, payload)

          this.$store.commit('editEv', {
            EvBestFitness: EvBestFitness,
            EvBestAccuracy: EvBestAccuracy
          })

          this.tableDatas = []
          for (let i = 0; i < this.numOfNetwork; i++) {
            this.tableDatas.push([{
              id: i + 1,
              mfea: bestobj[i],
              dijkstra: bestobj_dijk[i],
              mfeaPath: bestInd_data[i],
              dijkstraPath: bestInd_data_dijk[i]
            }])
          }

          let xAxisData = []
          for (let i = 1; i <= this.form.generationCount; i++) {
            xAxisData.push(i)
          }
          let legendData = []
          for (let i = 1; i <= this.numOfNetwork; i++) {
            legendData.push(`网络${i}`)
          }

          // 绘制图表
          this.lineOptions = {
            xAxis: {
              type: 'category',
              data: xAxisData
            },
            yAxis: {
              type: 'value'
            },
            legend: {
              data: legendData
            },
            tooltip: {
              trigger: 'axis'
            },
            grid:{
              top: 30,
              left: 30,
              right: 0,
              bottom: 30
            },
            series: EvBestFitness.map((arr, index) => ({data: arr, type: 'line', smooth: true, name: `网络${index + 1}`}))
          }

          this.accuracyLineOptions = {
            xAxis: {
              type: 'category',
              data: xAxisData
            },
            yAxis: {
              type: 'value'
            },
            legend: {
              data: legendData
            },
            tooltip: {
              trigger: 'axis'
            },
            grid:{
              top: 30,
              left: 30,
              right: 0,
              bottom: 30
            },
            series: EvBestAccuracy.map((arr, index) => ({data: arr, type: 'line', smooth: true, name: `网络${index + 1}`}))
          }


          for (let t = 0; t < this.numOfNetwork; t++) {
            const nodes = []
            const dim = Mats[t].length - 1
            for (let i = 1; i <= dim; i++) {
              nodes.push({id: i, title: i, shape: 'dot', size: 6})
            }

            const path = bestInd_data[t]
            for (let item of path) {
              nodes[item - 1] = {id: item, label: item + '', shape: 'dot', size: 15, font: {size: 30}}
            }
            nodes[startNode - 1] = {id: startNode, label: startNode + '', shape: 'diamond', font: {size: 30}, color: 'yellow'}
            nodes[endNode - 1] = {id: endNode, label: endNode + '', shape: 'diamond', font: {size: 30}, color: 'yellow'}

            const edges = []
            const Mat = Mats[t]
            let nodeId = 0
            for (let i = 1; i<= dim; i++) {
              for (let j = 1; j <= dim; j++) {
                if (i < j) {
                  if (Mat[i][j]) {
                    edges.push({
                      from: i, 
                      to: j,
                      id: nodeId++,
                      // label: Mat[i][j] + '',
                      font: {
                        // color: 'rgba(0, 0, 0, 0)',
                        align: 'top',
                        size: 20
                      },
                      chosen: {
                        label: function(values, id, selected, hovering) {
                          // values.color = 'black'
                          edges[id].label = Mat[i][j] + ''
                          // values.size = 20
                        }
                      }
                    })
                  }
                }
              }
            }

            for (let i = 0; i < path.length - 1; i++) {
              const from = path[i]
              const to = path[i + 1]
              edges.push({
                from: from, 
                to: to, 
                color: 'red',
                label: Mat[from][to] + '',
                font: { align: "top", size: 23 },
                chosen: {label: true}
              })
            }

            const data = {nodes: new vis.DataSet(nodes), edges: new vis.DataSet(edges)}
            const container = document.getElementById(`mat${t + 1}`)
            let options = {
              layout: {    
                improvedLayout: false
              },
              physics: {
                forceAtlas2Based: {
                  gravitationalConstant: -2,
                  centralGravity: 0.005,
                  springLength: 180,
                  springConstant: 0.1,
                },
                maxVelocity: 0,
                // minVelocity: 0,
                solver: "forceAtlas2Based",
                timestep: 1,
                stabilization: { iterations: 150 }
              },
              interaction: {
                tooltipDelay: 200
              },
            }
            let network = new vis.Network(container, data, options)
            let tag = true
            network.on('afterDrawing', () => {
              if (tag) {
                if (t === this.numOfNetwork - 1) {
                  this.loading = false
                  this.disabled = false
                }
                tag = false
              }
            })
            network.on('selectEdge', ({edges}) => {
            })
          }
        } else {
          this.$message.error('请输入合法的起点和终点')
          return
        }
      })
    }
  }
}
</script>

<style scoped lang="scss">
.result {
  .mat-wrap {
    display: grid;
    grid-gap: 20px;
    grid-template-columns: repeat(3, 1fr);
    .mat {
      margin-bottom: 20px;
      border: solid 1px #90EE90;
      .graph {
        height: 400px;
      }
    }
  }
  .chart {
    height: 200px;
  }
}
</style>

<style lang="scss">
.result {
  .el-input__inner:focus {
    border-color: #90EE90;
  }
  .el-button--text {
    color: #90EE90;
  }
}
</style>
